.app-loader__overlay {
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-loader {
  position: relative;
  width: 200px;
  height: 200px;
}

.app-loader div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}

.app-loader div:nth-child(2) {
  animation-delay: -0.5s;
}

@keyframes lds-ripple {
  0% {
    top: 96px;
    left: 96px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: 0px;
    left: 0px;
    width: 192px;
    height: 192px;
    opacity: 0;
  }
}